<template lang="pug">
.curso-main-container.pb-3
  BannerInterno
  .container.tarjeta.tarjeta--blanca.p-4.p-md-5.mb-5
    .titulo-principal.color-acento-contenido
      .titulo-principal__numero
        span 1
      h1 Diagnosticar capacidades de innovacin
    p.mb-5 En la actualidad, existen mltiples definiciones relacionadas con el concepto de innovacin, las cuales tienen su principal variacin de acuerdo con el enfoque sectorial. Para el presente contenido y teniendo en cuenta el enfoque del programa de formacin, se definir la innovacin como la inclusin de diferentes mtodos, materiales o tecnologas que generen un nuevo producto o servicio con mayores niveles de sostenibilidad, en una o varias etapas del proceso productivo, procesos administrativos o proceso comercial.
    .row.justify-content-center.mb-5
      .col-lg-10
        .bloque-texto-a.color-secundario.p-3.p-md-4
          .row.m-0.align-items-center.justify-content-between
            .col-lg-8.mb-4.mb-lg-0
              .bloque-texto-a__texto.p-4
                p La innovacin tambin es considerada como aquel diferencial que permite a las organizaciones ser ms competitivas, debido a que las nuevas tendencias de globalizacin y caractersticas del consumidor requieren la inclusin de nuevos productos con propiedades innovadoras que generen nuevas ventas y compitan con organizaciones de todo el mundo.
            .col-lg-4
              figure
                img(src='@/assets/curso/tema1/img1.png', style="width: 400px").m-auto
    .row.mb-5
      .col-12
        .bg-c1
          .row.align-items-center
            .col-lg-auto.mb-4.mb-lg-0
              figure
                img(src='@/assets/curso/tema1/img1.svg', style="width: 80px").m-auto
            .col-lg
              p.mb-0 Sin embargo, este proceso de generar nuevos productos y servicios lleva implcito la innovacin en los procesos, de manera que permitan fabricar de forma ms competitiva, administrar organizaciones constantemente cambiantes y adaptacin al mercado con nuevas tcnicas de comercializacin. #[b Para definir los diferentes niveles de innovacin en la produccin de bienes y servicios sostenibles, es de vital importancia establecer los diferentes tipos de sostenibilidad, siendo los principales:]
    TabsA.color-acento-botones.mb-5
      .tarjeta.color-acento-botones--borde.p-4(titulo="Sostenibilidad ambiental")
        .row.align-items-center
          .col-lg-8.mb-4.mb-lg-0
            h4 Sostenibilidad ambiental
            p Es el trmino utilizado comnmente para establecer las buenas prcticas de produccin y de gestin que buscan cuidar los recursos naturales en cualquiera de las etapas del proceso. Algunos ejemplos son: seleccin de materiales eco amigables, tcnicas de produccin utilizando energas renovables, como son la solar o la elica, para el funcionamiento de mquinas; mtodos de comercializacin que generan una cultura medioambiental, como son utilizar bolsas de mltiples usos, gestin de residuos, entre lo cual se incluye el proceso de reciclaje y recuperaciones, entre otros. El presente componente de formacin tendr como principal enfoque la sostenibilidad, porque en esta se fundamenta la produccin de bienes y servicios.
          .col-lg-4
            figure
              img(src='@/assets/curso/tema1/img2.svg', style="width: 350px").m-auto
      .tarjeta.color-acento-botones--borde.p-4(titulo="Sostenibilidad social")
        .row.align-items-center
          .col-lg-8.mb-4.mb-lg-0
            h4 Sostenibilidad social
            p Sus principales caractersticas se enfocan en generar condiciones justas laborales para los empleados de una empresa. En el contexto colombiano, se refiere al pago de los parafiscales a los empleados, estabilidad laboral evitando contratos por temporadas, proporcionar los elementos de proteccin personal y poder laborar en condiciones seguras.
          .col-lg-4
            figure
              img(src='@/assets/curso/tema1/img3.svg', style="width: 350px").m-auto
      .tarjeta.color-acento-botones--borde.p-4(titulo="Sostenibilidad econmica")
        .row.align-items-center
          .col-lg-8.mb-4.mb-lg-0
            h4 Sostenibilidad econmica
            p Equivalente a las acciones que permiten generar un proceso sostenible de las condiciones del mercado para las organizaciones. En el contexto colombiano, se enfocar en las acciones utilizadas para evitar el contrabando, plagio (piratera) de productos, estabilidad de precios, ya sean generados por la inflacin; acuerdos comerciales de libre comercio, entre otros.
          .col-lg-4
            figure
              img(src='@/assets/curso/tema1/img4.svg', style="width: 350px").m-auto
    p Para desarrollar una medicin de las caractersticas y estado actual de una organizacin respecto a la innovacin, es necesario definir las variables y conceptos que se van a analizar en su estado actual, las cuales posteriormente debern ser comparadas con las caractersticas de un referente o estado ideal, de esta manera se podr establecer el estado de innovacin. 
    p.mb-5 Para ampliar el concepto de innovacin, lo invitamos a ver el video #[b Conceptos de Innovacin y caracterizacin del usuario: introduccin, el cual se encuentra en el material complementario].
    Separador
    #t_1_1
    .titulo-segundo.color-acento-botones
      h2 1.1 Tipos de innovacin
    p.mb-5 La innovacin puede generar impactos positivos en los diferentes tipos de sostenibilidad, para lo cual es importante conocer los principales tipos de innovacin.
    .row.mb-4
      .col-auto.p-3.bg-c2
        h4.mb-0 Innovacin de producto
    p.mb-5 #[b Equivalente a la inclusin de un nuevo bien o servicio o la mejora radical de las caractersticas de alguno de ellos]. Para realizar esta innovacin, pueden incluirse diferentes factores, ya sea en conocimiento, materiales, tecnologa, diseo, entre otros, pero es importante resaltar que la innovacin en el producto debe ser percibida y aceptada por el consumidor final.
    .row
      .col-lg.mb-4.mb-lg-0
        .cajon.color-primario.p-4
          p.mb-0 Dentro de este tipo de innovacin, est la telefona, en la que se ha mantenido en el tiempo el concepto de facilitar la comunicacin entre diferentes personas.
      .col-lg.mb-4.mb-lg-0
        .cajon.color-primario.p-4
          p.mb-0 Esta forma de comunicacin ha tenido una evolucin en innovacin, variando desde un telfono con cable fijo, en el hogar, hasta telfonos inteligentes.
      .col-lg
        .cajon.color-primario.p-4.fig-p2
          p.mb-0 Los #[i smartphones] permiten al usuario comunicarse desde diferentes lugares, con conexiones inalmbricas, e incluyen medios de comunicacin alternativos y complementarios a la voz, como son la mensajera de texto y la mensajera multimedia, entre otros.
    .row.bg-c3.mb-5
      .col-12
        figure.fig-p1
          img(src='@/assets/curso/tema1/img5.svg', style="width: 850px").m-auto
    p.mb-5 En este mismo ejemplo de innovacin del telfono, se pueden incluir elementos sostenibles como lo son la larga vida de las bateras y los sistemas de carga ms rpida, los cuales reducen los requerimientos energticos.
    .row.mb-4
      .col-auto.p-3.bg-c2
        h4.mb-0 Innovacin de proceso
    p.mb-5 En este tipo de innovacin, se incluyen los diferentes mtodos, tcnicas o conocimiento aplicados, que permiten mejorar la gestin de la produccin o las actividades de apoyo, como la logstica, buscando disminuir los costos, aumentar la productividad, incrementar los estndares de calidad y mejorar el uso de los recursos de la empresa. Algunos ejemplos se observan en el grfico a continuacin:
    .row.justify-content-center.mb-5
      .col-lg-12
        ImagenInfografica.color-secundario
          template(v-slot:imagen)
            figure
              img.img-a.img-t(src='@/assets/curso/tema1/img7.svg',alt="La imagen presenta diferentes ejemplos de la innovacin de procesos, como son: equipos automatizados, software y hardware de distribucin, sostenibilidad y utilizacin de energas renovables.")
          .tarjeta.color-secundario.p-3(x="50%" y="0%" numero="+")
            .h5.mb-2 Equipos automatizados
            p Los cuales permiten fabricar productos las 24 horas, en los siete das de la semana, sin necesitar personal.
          .tarjeta.color-secundario.p-3(x="25%" y="50%" numero="+")
            .h5.mb-2 Utilizacin de energas renovables
            p Incluir vehculos, en operaciones logsticas, que utilizan combustibles no fsiles (biodisel, carros elctricos, entre otros) reduce el impacto de la huella de carbono en el medio ambiente.
          .tarjeta.color-secundario.p-3(x="50%" y="100%" numero="+")
            .h5.mb-2 Sostenibilidad
            p Si incluimos en estos ejemplos de innovacin de proceso la sostenibilidad, se puede contar con equipos de bajo consumo energtico que utilizan energas renovables.
          .tarjeta.color-secundario.p-3(x="75%" y="50%" numero="+")
            .h5.mb-2 #[i Software] y #[i hardware] de distribucin
            p Para controlar la distribucin, indicando las mejores rutas de viaje y permitiendo realizar trazabilidad en la entrega de productos, generando menores consumos de combustible y facilitando efectuar entregas en menor tiempo.
    .row.mb-4
      .col-auto.p-3.bg-c2
        h4.mb-0 Innovacin en materiales
    p.mb-5 Esta innovacin se encuentra relacionada con los tipos de innovacin anteriores, porque se refiere a la inclusin de materiales e insumos con propiedades mejoradas a las convencionales, como lo pueden ser materiales eco amigables y/o materiales recuperados/reciclados, con los cuales se pueden generar productos innovadores o variar los componentes innovadores del proceso, algunos de ellos son:
    .row.h-100.justify-content-evenly.mb-5
      .col-md-8.col-lg-5.mb-4.mb-lg-0
        .row
          .col-12.p-4.bg-c4
            figure
              img(src='@/assets/curso/tema1/img3.png')
          .col-12.p-4.bg-c5
            h5 Plstico reciclado
            p.mb-0 Para las organizaciones colombianas, algunos ejemplos de estas innovaciones son las botellas para bebidas elaboradas en plstico reciclado, lo que les genera beneficios tributarios por ser empresas sostenibles y, adicionalmente, generan campaas de cuidado medioambiental, que buscan incrementar la preferencia del consumidor por la marca.
      .col-md-8.col-lg-5
        .row
          .col-12.p-4.bg-c6
            figure
              img(src='@/assets/curso/tema1/img2.png')
          .col-12.p-4.bg-c7
            h5 Productos orgnicos
            p.mb-0 Corresponden a productos alimenticios orgnicos, los cuales son libres de qumicos y fertilizantes, con componentes naturales que no afectan el medio ambiente y que no modifican las propiedades del producto.
    .row.mb-5
      .col-12
        .bg-c8
          .row.align-items-center
            .col-lg.mb-4.mb-lg-0
              p.mb-0 Este tipo de innovacin, por lo general, se encuentra afectada por los niveles de rentabilidad, debido a que los materiales sostenibles suelen ser ms costosos que los materiales convencionales. Por tal motivo, al incluir esta innovacin, se debe buscar ingresar a nuevos mercados para competir por diferenciales y no por precio; este diferencial de mercado se puede dar en las exigencias normativas en cuidados medioambientales.
            .col-lg-auto
              figure
                img(src='@/assets/curso/tema1/img8.svg', style="width: 80px").m-auto
    .row.mb-4
      .col-auto.p-3.bg-c2
        h4.mb-0 Innovacin organizacional
    p.mb-5 Busca implementar nuevos mtodos para realizar procesos de gestin organizacional que le permitan innovar y ser ms competitivos. La diferencia con la innovacin en los procesos, se fundamenta en que la innovacin organizacional se origina para los niveles directivos y busca variar los procesos administrativos.
    .row.justify-content-center.mb-5
      .col-md-8.col-lg-10
        .row
          .col-lg-8.p-4.bg-c9.order-2.order-lg-1
            figure.mb-4
              img(src='@/assets/curso/tema1/img9.svg', style="width: 80px")
            p.mb-0 Los principales ejemplos son los modelos de gestin y mejoramiento continuo, como lo han sido las filosofas #[i Lean Manufacturing] (produccin limpia), las cuales buscan eliminar actividades que no generan valor agregado, como la reduccin de desperdicio, que impacta en la sostenibilidad porque solo se utiliza lo que realmente se requiere.
          .col-lg-4.p-0.order-1.order-lg2
            figure
              img(src='@/assets/curso/tema1/img4.png')
    .row.mb-4
      .col-auto.p-3.bg-c2
        h4.mb-0 Innovacin comercial
    p.mb-5 Consiste en los mtodos, tcnicas y estrategias innovadoras utilizadas en los diferentes canales de comercializacin de un producto o servicio que permiten generar el diferencial ante el consumidor, lo cual ocasiona mayores niveles de venta.  Este tipo de innovacin, en los ltimos aos, ha sido impactado por el amplio uso de la tecnologa, el cual ha incentivado el uso de canales virtuales para la comercializacin de cualquier tipo de bien o servicio. A continuacin, se puede observar un ejemplo de innovacin comercial:
    .row.bg-c10
      .col-12.p-5
        .row.align-items-center
          .col-lg-7.mb-4.mb-lg-0
            figure
              img(src='@/assets/curso/tema1/img11.svg', style="width: 550px").m-auto
          .col-lg-5
            .row
              .col-12.bg-c11.mb-4.p-3
                figure.mb-2
                  img(src='@/assets/curso/tema1/img13.svg', style="width: 60px")
                p.mb-0 En este tipo de innovacin, se permite tener un punto de venta abierto 24 horas, los siete das de la semana, disponible en Internet, para cualquier usuario en todo el mundo.
              .col-12.bg-c11.mb-4.p-3
                figure.mb-2
                  img(src='@/assets/curso/tema1/img14.svg', style="width: 60px")
                p.mb-0 Estas tiendas estn diseadas para que el usuario pueda comprar productos  navegando por las diferentes opciones de forma autnoma, y el pago lo puede realizar en la misma plataforma.
              .col-12.bg-c11.p-3
                figure.mb-2
                  img(src='@/assets/curso/tema1/img15.svg', style="width: 60px")
                p.mb-0 Algunas tiendas tienen  tiempos de entrega mnimos, que pueden llegar a ser de unos pocos minutos si son entregas de productos alimenticios; inclusive, lapsos inferiores a 24 horas para otro tipo de bienes, como son electrodomsticos y prendas de vestir, entre otros.
    .row.mt-5
      .col-12
        .bg-c1
          .row.align-items-center
            .col-lg-auto.mb-4.mb-lg-0
              figure
                img(src='@/assets/curso/tema1/img16.svg', style="width: 80px").m-auto
            .col-lg
              p.mb-0 Con respecto a la sostenibilidad, en las tiendas virtuales, se destaca que ya no se requiere contar con instalaciones fsicas, donde se necesite hacer uso de una iluminacin durante toda la jornada, adicionalmente, la publicidad en estos canales es digital, lo cual elimina el uso de papel en volantes, tarjetas y dems elementos de material de promocin y publicidad tradicional.                
    Separador
    #t_1_2
    .titulo-segundo.color-acento-botones
      h2 1.2 Diagnstico, recoleccin y anlisis de informacin
    p Se puede definir un diagnstico como una fotografa inicial con la evaluacin de las variables establecidas, con el fin de conocer el estado en que se encuentra un individuo u organizacin, buscando reconocer las debilidades, necesidades y fortalezas.
    .row.h-100.justify-content-evenly.mb-5
      .col-md-9.col-lg-5.mb-4.mb-lg-0
        .row
          .col-12.bg-c12.p-4
            figure
              img(src='@/assets/curso/tema1/img17.svg', style="width: 350px").m-auto
          .col-12.p-4.bg-c5
            p.mb-0 Aplicando el concepto a la gestin de la innovacin en la produccin de bienes o servicios sostenibles, un diagnstico se realiza para identificar las oportunidades de mejoramiento y sostenibilidad de la fabricacin y rentabilidad de los productos.
      .col-md-9.col-lg-5.mb-4.mb-lg-0
        .row
          .col-12.bg-c13.p-4
            figure
              img(src='@/assets/curso/tema1/img19.svg', style="width: 350px").m-auto
          .col-12.p-4.bg-c7
            p.mb-0 Para lo que se requiere realizar la evaluacin minuciosa de cada uno de los pasos, desde el diseo del producto hasta la comercializacin, con el fin de conocer el estado de cada uno de estos procesos, donde se podr conocer si existen falencias o necesidades especficas a resolver, que mejoren la productividad y sostenibilidad desde la innovacin en la empresa.
    p.mb-5 En un diagnstico de innovacin sostenible, se busca caracterizar las fortalezas y necesidades por satisfacer dentro de una empresa, enfocndose en los impactos medioambientales desde los diferentes tipos de innovacin, utilizndolos en el conocimiento, materiales, procesos, tecnologa, entre otros. Aunque existen mltiples formas de realizar un diagnstico, mnimo se debern tener en cuenta las siguientes variables asociadas a la sostenibilidad:

    SlyderF.mb-5(columnas="col-lg-6 col-xl-4")
      .tarjeta.bg-c14.p-4    
        img.img-t.mb-4(src='@/assets/curso/tema1/img21.svg')
        p.mb-0 Capacidades para disear y desarrollar productos o servicios con un diferencial innovador ante la competencia, el cual deber ser aceptado por el consumidor y tener enfoques de cuidados sostenibles.
      .tarjeta.bg-c15.p-4    
        img.img-t.mb-4(src='@/assets/curso/tema1/img22.svg')
        p.mb-0 Materiales e insumos incorporados en la fabricacin que incluyan caractersticas biodegradables, recicladas o recuperadas.
      .tarjeta.bg-c14.p-4    
        img.img-t.mb-4(src='@/assets/curso/tema1/img23.svg')
        p.mb-0 Tecnologa disponible para realizar procesos innovadores, que incrementen los niveles de sostenibilidad.
      .tarjeta.bg-c15.p-4    
        img.img-t.mb-4(src='@/assets/curso/tema1/img24.svg')
        p.mb-0 Inclusin en los procesos de comercializacin de nuevas tcnicas y formas innovadoras para vender, que sean aceptadas por el cliente y generen mayores niveles de rentabilidad econmica, a los que se les deber medir beneficios medioambientales.
      .tarjeta.bg-c14.p-4    
        img.img-t.mb-4(src='@/assets/curso/tema1/img25.svg')
        p.mb-0 Conocimiento del talento humano en procesos, modelos y tcnicas de innovacin, desarrollo y sostenibilidad.
      .tarjeta.bg-c15.p-4    
        img.img-t.mb-4(src='@/assets/curso/tema1/img26.svg')
        p.mb-0 Recursos econmicos disponibles para generar cambios organizacionales que favorezcan la sostenibilidad.
      .tarjeta.bg-c14.p-4    
        img.img-t.mb-4(src='@/assets/curso/tema1/img27.svg')
        p.mb-0 Cultura del cambio organizacional, en la cual se deber medir la facilidad del cambio y la frecuencia con que se realiza en las empresas, para conocer los niveles de resistencia y aceptacin en las diferentes reas de la organizacin.
      .tarjeta.bg-c15.p-4    
        img.img-t.mb-4(src='@/assets/curso/tema1/img28.svg')
        p.mb-0 Prcticas sostenibles en la organizacin, donde se debern contemplar manejos de los residuos, procesos de reciclaje, rediseos de productos, utilizaciones de energas renovables, condiciones laborales, entre otros.
    .tarjeta.color-primario.p-3.mb-5
      .row.justify-content-around.align-items-center
        .col-3.col-sm-2.col-lg-1
          img(src="@/assets/curso/tema1/img29.svg")
        .col
          .row.justify-content-between.align-items-center
            .col.mb-3.mb-sm-0
              h3.mb-1 Infografa. Fuentes, instrumentos y recursos para recolectar datos
              p Para realizar el proceso de recoleccin de informacin en el diagnstico de las variables anteriormente indicadas, es necesario contemplar el tipo de fuente para obtener los datos y los instrumentos y recursos para recoger esos datos de acuerdo con el tipo de fuente, como se muestra a continuacin.
            .col-sm-auto
              a.boton.color-acento-contenido.texto-blanco(:href="obtenerLink('downloads/Infografia_Fuentes_Datos.pdf')" target="_blank")
                span Descargar
                i.fas.fa-file-download
    p.mb-5 Para complementar la informacin, lo invitamos a ver el video #[b Recoleccin de datos], el cual se encuentra en el material complementario.

    p.mb-5 Posteriormente a la recoleccin de la informacin, es necesario acondicionarla, organizarla, clasificarla y analizarla, para generar un proceso de evaluacin que se llama diagnstico, que deber finalizar con una serie de recomendaciones. A continuacin, se presentan las principales caractersticas de estos elementos:
    .row.bg-c10
      .col-12.p-5
        .row.mb-4
          .col-auto.p-3.bg-c2
            h4.mb-0 Acondicionar la informacin
        .row.align-items-center.mb-5
          .col-lg-8.mb-4.mb-lg-0
            p En esta etapa, se debe validar que todos los datos se encuentren en las mismas escalas de variables y con los mismos tipos de datos. En caso de que no se encuentren, deben realizarse sus respectivas equivalencias. 
            p Dentro del acondicionamiento de la informacin, es importante contemplar la conversin de los resultados cualitativos a cuantitativos, es decir, convertir las percepciones de las personas entrevistadas en escalas de calificacin estandarizadas, con el fin de poder realizar anlisis y evaluacin de los resultados. Algunos ejemplos son:
          .col-lg-4
            figure
              img(src='@/assets/curso/tema1/img30.svg', style="width: 350px").m-auto
        .row.justify-content-center.mb-5
          .col-md-8.col-lg-6.mb-4.mb-lg-0
            .row.m-1.h-100.bg-c16
              .col-lg-auto.p-4.mb-4.mb-lg-0.bg-c16a
                figure
                  img(src='@/assets/curso/tema1/img31.svg', style="width: 70px").m-auto
              .col
                .row.align-items-center.h-100
                  .col
                    p.mb-lg-0 Los datos de los tiempos de produccin son suministrados por unas fuentes en minutos y otras en segundos, razn por la cual deben dejarse en la misma unidad de medida para ser analizados.
          .col-md-8.col-lg-6
            .row.m-1.h-100.bg-c16
              .col-lg-auto.p-4.mb-4.mb-lg-0.bg-c16a
                figure
                  img(src='@/assets/curso/tema1/img32.svg', style="width: 70px").m-auto
              .col
                .row.align-items-center.h-100
                  .col
                    p.mb-lg-0 Las cifras de costos y ventas pueden ser expresadas en dlares si son productos importados o exportados, mientras que en productos locales estn en pesos; razn por la cual debe establecerse la equivalencia.
        .row.mb-4
          .col-auto.p-3.bg-c2
            h4.mb-0 Organizacin y clasificacin de la informacin
        .row.mb-5
          .col-12
            .bg-c1
              .row.align-items-center
                .col-lg-auto.mb-4.mb-lg-0
                  figure
                    img(src='@/assets/curso/tema1/img33.svg', style="width: 80px").m-auto
                .col-lg
                  p.mb-0 En esta actividad, se deben unificar todos los datos recolectados en una misma base de datos, ordenados en el orden de las variables establecidas, para posteriormente ser clasificados en cada uno de los tipos de innovacin y sostenibilidad. Un ejemplo de este proceso es organizar los datos recolectados por diferentes reas de la empresa en una misma base de datos, para posteriormente clasificarlos en las variables de cada tipo de innovacin.
        .row.mb-4
          .col-auto.p-3.bg-c2
            h4.mb-0 Diagramacin de los resultados
        p.mb-5 La tcnica ms utilizada para facilitar los procesos de anlisis de la informacin es la generacin de grficos con cifras y porcentajes de cada uno de los comportamientos de las variables, ya que es un instrumento sencillo tanto en su elaboracin como en su interpretacin. Algunos ejemplos son:
        .row.justify-content-center.mb-4
          .col-lg-10.p-4.bg-c11
            .row
              .col-lg-6.mb-4.mb-lg-0
                p Grficos histricos de los comportamientos de las inversiones en innovacin, en los cuales se puede visualizar, de una forma fcil, el incremento de los recursos econmicos asignados por cada ao en una empresa, para lo cual se sugiere presentar, en columnas o barras, la informacin.
              .col-lg-6
                figure
                  img(src='@/assets/curso/tema1/img34.svg', style="width: 550px").m-auto
        .row.justify-content-center.mb-5
          .col-lg-10.p-4.bg-c11
            .row
              .col-lg-6.mb-4.mb-lg-0
                p Porcentajes de distribucin de las oportunidades de innovar para mejorar las condiciones sostenibles de los residuos en la empresa, en los cuales es recomendable cambiar el tipo de grfico a tortas con porcentajes, debido a que se debe analizar cul es la distribucin de las oportunidades de mejora.
              .col-lg-6
                figure
                  img(src='@/assets/curso/tema1/img35.svg', style="width: 550px").m-auto
        .row.mb-4
          .col-auto.p-3.bg-c2
            h4.mb-0 Anlisis y evaluacin de la informacin
        p.mb-5 Los resultados de los datos recolectados son solo cifras y grficas sin un anlisis, es por ello que esta actividad es una de las ms importantes y debe ser realizada por un experto en cada temtica, porque en esta etapa se deben identificar el comportamiento, las causales, los efectos de cada una de las variables y, en algunos casos, puede ser anlisis multivariable, en el cual se cruzan comportamientos de los resultados de mltiples variables. Existen mltiples formas para realizar los anlisis y evaluacin de los resultados, pero algunos de ellos son:
        .row.justify-content-center.mb-4
