            .row
              .col-12
                p.mb-5 #[b Anlisis en el diagrama Causa Efecto]. En este ejemplo, se busca determinar por qu un producto no cumple con las caractersticas de innovacin sostenible, para lo que se establecern las races de este proceso, las cuales debern estar soportadas en los datos recolectados. A continuacin, se presenta un ejemplo:
