                  p.mb-0 Por ltimo, el resultado de la evaluacin se debe indicar como conclusiones o un resumen de la situacin actual del consolidado, a lo cual se le llama el diagnstico, y contiene el informe de resultados, para lo cual existen diferentes formas de presentacin, entre las cuales la ms comn es un informe tcnico donde se presenta la metodologa de recoleccin de la informacin, el anlisis de las cifras y las conclusiones finales, que  generarn propuestas de mejora para lograr modelos de negocio innovadores y sostenibles.
    Separador
    #t_1_3
    .titulo-segundo.color-acento-botones
      h2 1.3 Modelos de negocio
    p.mb-5 Los negocios son la forma como se obtiene una rentabilidad econmica de una inversin inicial, por lo tanto, un modelo de negocio es la manera en que se estructura una organizacin o una persona para obtener utilidades. Dicha estructura puede variar en complejidad, segn sea el tamao del negocio, y principalmente se clasifican en:
    TabsB.color-acento-botones.mb-5
      .py-4.py-md-5(titulo="Modelo de negocio en la fabricacin" :icono="require('@/assets/curso/tema1/img38.svg')")
        .row.justify-content-center
          .col-lg-5.mb-4.mb-lg-0
            p En esta estructura se obtienen utilidades por medio de la transformacin de materias primas para ser convertidas en un producto terminado, el cual se comercializa con los recursos propios de la empresa o por medio de otra organizacin. Su rentabilidad se encuentra en vender a un mayor precio el producto final con respecto a la suma de los costos asociados a la produccin.
          .col-lg-5
            figure
              img(src='@/assets/curso/tema1/img5.png', style="width: 450px").m-auto
      .py-4.py-md-5(titulo="Modelo de negocio en comercializacin" :icono="require('@/assets/curso/tema1/img39.svg')")
        .row.justify-content-center
          .col-lg-5.mb-4.mb-lg-0
            p Se fundamenta en obtener ganancias por la compra y venta de productos terminados, sin realizar ningn proceso de produccin. Este modelo puede tener diferentes niveles, desde la exportacin e importacin de productos para ser comercializados en el destino, hasta compras de grandes volmenes de mercanca para ser vendidos de forma individual.
          .col-lg-5
            figure
              img(src='@/assets/curso/tema1/img6.png', style="width: 450px").m-auto
      .py-4.py-md-5(titulo="Modelo de negocio de servicios" :icono="require('@/assets/curso/tema1/img40.svg')")
        .row.justify-content-center
          .col-lg-5.mb-4.mb-lg-0
            p Existe una tercera manera de obtener utilidades sin necesidad de fabricar o comercializar producto, la cual consiste en prestar servicios de apoyo, ya sea para la produccin como para la venta. Estos servicios de apoyo, por lo general, no son tangibles para el consumidor final, pero sin la existencia de estos modelos de negocio muchas organizaciones no podran funcionar. Algunos ejemplos son: los bancos, quienes se encargan de manejar y custodiar el dinero de las empresas, las empresas de seleccin y contratacin de personal, las empresas dedicadas al desarrollo de #[i software], las empresas de transporte y almacenamiento de productos, entre otros.
          .col-lg-5
            figure
              img(src='@/assets/curso/tema1/img7.png', style="width: 450px").m-auto
    p.mb-5 Adicionalmente a la clasificacin anterior, pueden generarse diferentes niveles de especializacin de los modelos de negocio, segn sea el sector econmico, los cuales se estudiarn a continuacin.
    .row.mb-4
      .col-auto.p-3.bg-c2
        h4.mb-0 A. Modelos de negocio innovadores
    p.mb-5 El concepto de modelo significa la manera de alcanzar un fin, este fin puede ser un producto o un servicio, segn sea el enfoque empresarial, razn por la cual se puede indicar que el modelo es la manera ideal de cmo se debe realizar un proceso para obtener un resultado esperado. Un prototipo terminado recibe tambin el nombre de modelo, porque en l se encuentran las especificaciones finales requeridas para ser replicadas posteriormente en la produccin del bien o servicio.
    .row.justify-content-center.mb-5
      .col-lg-10
        .bloque-texto-a.color-primario.p-3.p-md-4
          .row.m-0.align-items-center.justify-content-between
            .col-lg-4.mb-4.mb-lg-0
              figure
                img(src='@/assets/curso/tema1/img8.png', style="width: 400px").m-auto
            .col-lg-8
              .bloque-texto-a__texto.p-4
                p La innovacin, al ser considerada como el diferencial que permite a las organizaciones ser ms competitivas en un modelo de negocio, deber ser uno de los componentes e instrumentos metodolgicos para obtener la rentabilidad econmica en una empresa, ya que se buscar producir bienes o servicios con diferenciales y que generen rentabilidad econmica en las organizaciones.
    p.mb-5 Para realizar un modelo de negocio innovador, existen mltiples autores referentes, pero todos coinciden en afirmar que un modelo de negocio innovador equivale a producir y/o comercializar bienes o servicios de una forma diferente a lo cotidiano, es decir, pensar y actuar diferente. Para explicar esta forma de pensar y actuar diferente, se tomar de ejemplo la estrategia del Ocano Azul, una teora creada por W. Chan Kim y Rene Mauborgne, en el ao 2005, y se fundamenta con uno de los principales pilares en la innovacin, en el cual se comparan dos grandes estrategias de negocios:
    .row.h-100.justify-content-evenly.mb-5
      .col-md-8.col-lg-5.mb-4.mb-lg-0
        .row
          .col-12.p-4.bg-c17
            figure.mb-4
              img(src='@/assets/curso/tema1/img9.png')
            .row.justify-content-center
              .col-auto.bg-c17a.p-3
                h4.mb-0 Ocano Rojo
          .col-12.p-4.bg-c18
            h5 Modelos de negocio tradicionales
            ul.lista-ul
              li 
                i.fas.fa-book(style="color: #583CB6")
                | Competir con productos o servicios en nichos de mercado existentes.
              li 
                i.fas.fa-book(style="color: #583CB6")
                | Priorizar los elementos de la empresa y el producto para generar menores precios y costos como diferenciales para posicionarse en el mercado.
              li 
                i.fas.fa-book(style="color: #583CB6")
                | Buscar competir constantemente con la competencia por un mismo segmento de clientes.
      .col-md-8.col-lg-5
        .row
          .col-12.p-4.bg-c19
            figure.mb-4
              img(src='@/assets/curso/tema1/img10.png')
            .row.justify-content-center
              .col-auto.bg-c19a.p-3
                h4.mb-0 Ocano Azul
          .col-12.p-4.bg-c20
            h5 Modelos de negocio innovadores
            ul.lista-ul
              li 
                i.fas.fa-book(style="color: #583CB6")
                | Crear nuevos nichos de mercado y lugares para el consumo, en los cuales no existe competencia.
              li 
                i.fas.fa-book(style="color: #583CB6")
                | Fundamentar la creacin de productos o servicios innovadores, en los cuales el precio no sea el factor diferencial para la compra.
              li 
                i.fas.fa-book(style="color: #583CB6")
                | Conocer al consumidor para satisfacer nuevas necesidades insatisfechas por la demanda existente.
              li 
                i.fas.fa-book(style="color: #583CB6")
                | Garantizar la viabilidad y rentabilidad del negocio por medio de una estrategia de posicionamiento con productos y servicios que la competencia no tenga y sean valorados por el consumidor.
    p.mb-5 Existe una multitud de #[b modelos de negocio innovadores], pero algunos ejemplos de estos tipos de modelo son:
    figure
      .video
        iframe(width="560" height="315" src="https://www.youtube.com/embed/6p_MNh0oQu4?si=yEdDZjpUQ3E789o3" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)
    figcaption.mb-5 Video. Tipos de modelos de negocio innovadores 
    .row.mb-4
      .col-auto.p-3.bg-c2
        h4.mb-0 B. Modelos de negocio sostenible
    p.mb-5 Es la destreza que promueve el uso de recursos tecnolgicos e innovadores para generar un impacto positivo en el medio ambiente y la sociedad, generando adicionalmente resultados financieros positivos para los modelos de negocio que los implementan.
    .row.align-items-center.mb-3
      .col-lg-4.mb-4.mb-lg-0
        figure
          img(src='@/assets/curso/tema1/img41.svg', style="width: 450px").m-auto
      .col-lg-8
        .row
          .col-12
            .fig-p4
              figure
                img(src='@/assets/curso/tema1/img43.svg', style="width: 70px")
          .col-12
            .row.justify-content-center.align-items-center
              .col-2.d-none.d-md-block
                .fig-p3
                  figure
                    img(src='@/assets/curso/tema1/img42.svg', style="width: 70px")
              .col-10.bg-c21.p-3.p-md-5
                p.mb-0.ms-4 Los modelos de negocio sostenibles ambientalmente han tenido mayor acogida en los ltimos aos, debido a la conciencia ambiental que se ha generado a nivel mundial, razn por la cual en varios segmentos de mercado, en especial para Europa o Norteamrica, el consumidor prefiere productos o servicios amigables con el medio ambiente sobre productos convencionales. 
          .col-12
            .fig-p5
              figure
                img(src='@/assets/curso/tema1/img44.svg', style="width: 70px")
    p.mb-5 #[b Esta categora de modelos sostenibles ambientalmente se encuentra fundamentada en la filosofa de las 7R, las cuales son:]
    TabsA.color-acento-botones
      .tarjeta.color-acento-botones--borde.p-4(titulo="Reciclar")
        .row
          .col-12
            h4 Reciclar
          .col-lg-4.mb-4.mb-lg-0
            figure
              img(src='@/assets/curso/tema1/img11.png', style="width: 400px").m-auto
          .col-lg-8
            p Consiste en generar modelos de negocio basados en la cultura de reutilizar y reprocesar, para la fabricacin de productos o servicios, materia prima proveniente de residuos o desechos de artculos del consumidor final o residuos industriales. 
            p En esta categora, se encuentra el plstico desechado de botellas, bolsas y dems elementos, para generar materia prima llamada PET (Poli Etiln Tereftalato), con la que se pueden producir prendas de vestir, nuevos empaques y una gran variedad de productos.
      .tarjeta.color-acento-botones--borde.p-4(titulo="Reducir")
        .row
          .col-12
            h4 Reducir
          .col-lg-4.mb-4.mb-lg-0
            figure
              img(src='@/assets/curso/tema1/img12.png', style="width: 400px").m-auto
          .col-lg-8
            p Se puede lograr mediante la reduccin de empaques para la comercializacin, as como tambin la disminucin de energa utilizada para el funcionamiento de equipos, lo que busca reducir el calentamiento global.
            p Se busca que el usuario industrial o final disminuya los niveles de consumo de productos o servicios que perjudiquen el medio ambiente.
      .tarjeta.color-acento-botones--borde.p-4(titulo="Reutilizar")
        .row
          .col-12
            h4 Reutilizar
          .col-lg-4.mb-4.mb-lg-0
            figure
              img(src='@/assets/curso/tema1/img13.png', style="width: 400px").m-auto
          .col-lg-8
            p Generar la conciencia ambiental en que un producto o servicio puede ser utilizado ms de una vez o para ms de una funcin, es la finalidad de esta filosofa.
            p Entre estos productos, estn las bolsas eco amigables de mltiples usos, para realizar compras.
      .tarjeta.color-acento-botones--borde.p-4(titulo="Reparar")
        .row
          .col-12
            h4 Reparar
          .col-lg-4.mb-4.mb-lg-0
            figure
              img(src='@/assets/curso/tema1/img14.png', style="width: 400px").m-auto
          .col-lg-8
            p Consiste en evitar desechar equipos, maquinaria, mobiliario o productos por fallos, cambiando la conciencia del consumidor, para que, antes de adquirir un nuevo elemento, repare el deterioro, lo que contribuye a disminuir el nmero de desechos. 
            p Existen variados ejemplos, como tapizar muebles, reparar la ropa o reparar equipos tecnolgicos.
      .tarjeta.color-acento-botones--borde.p-4(titulo="Renovar")
        .row
          .col-12
            h4 Renovar
          .col-lg-4.mb-4.mb-lg-0
            figure
              img(src='@/assets/curso/tema1/img15.png', style="width: 400px").m-auto
          .col-lg-8
            p Es actualizar equipos, muebles, maquinaria, productos, entre otros. Con este modelo de negocio, se busca generar sostenibilidad desde el concepto que un elemento no debe ser desechado por cambios de diseo o tecnologa, ya que existe la posibilidad de renovarlo para volver a ser competitivo. 
            p Un ejemplo son las actualizaciones de #[i software] o #[i hardware] para telfonos o computadores, en los que tambin se pueden renovar componentes como memorias o pantallas, para no desechar el equipo.
      .tarjeta.color-acento-botones--borde.p-4(titulo="Redisear")
        .row
          .col-12
            h4 Redisear
          .col-lg-4.mb-4.mb-lg-0
            figure
              img(src='@/assets/curso/tema1/img16.png', style="width: 400px").m-auto
          .col-lg-8
            p Para este modelo, desde la concepcin de la idea del producto o servicio, se generan cambios que mejoran el impacto en el medio ambiente, al incluir el uso de materiales biodegradables (eco amigables), o se varan las composiciones para tener un menor consumo. 
            p Algunos procesos son: la fabricacin de ropa con fibras 100 % naturales; disminuir la densidad de las botellas para reducir el consumo de plstico; elaborar cubiertos, platos y vasos en bamb y materiales biodegradables.
      .tarjeta.color-acento-botones--borde.p-4(titulo="Recuperar")
        .row
          .col-12
            h4 Recuperar
          .col-lg-4.mb-4.mb-lg-0
            figure
              img(src='@/assets/curso/tema1/img17.png', style="width: 400px").m-auto
          .col-lg-8
            p Este modelo de negocio se fundamenta en extraer componentes o piezas de un producto desechado para ser utilizado en la composicin o comercializacin de otros productos. Dentro de esta categora, estn las partes de un vehculo estrellado, del cual se recuperan las piezas en buen estado y se comercializan como repuestos.
    Separador
    #t_1_4
    .titulo-segundo.color-acento-botones
      h2 1.4 Mercadeo
    p.mb-5 Los mtodos para realizar la promocin y comercializacin de un producto o servicio hasta su venta son conocidos como mercadeo, el cual tiene diversas actividades que van desde la planeacin del producto o servicio, pasando por la estrategia de posicionamiento en un nicho de mercado, hasta la gestin comercial, en su promocin, venta o servicio posventa. #[b En el siguiente video, se pueden observar algunos ejemplos enfocados en la comercializacin de bienes y servicios innovadores y/o sostenibles de acuerdo con el tipo de mercadeo]:
    figure
      .video
        iframe(width="560" height="315" src="https://www.youtube.com/embed/BiuBBFz3gcI?si=48jlmwpDhwpzDbZe" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)
    figcaption.mb-5 Video. Comercializacin de bienes y servicios innovadores y sostenibles         
    .row.mb-4
      .col-auto.p-3.bg-c2
        h4.mb-0 Segmentacin de mercado
    p.mb-5 Es aquella que permite a las marcas crear estrategias para diferentes tipos de consumidores, teniendo en cuenta los valores de productos y servicios. La segmentacin es utilizada para optimizar los productos, la publicidad y las ventas. Para la innovacin y la sostenibilidad, es de vital importancia tener en cuenta la segmentacin, con el fin de posicionar el nuevo producto o servicio, la cual puede ser:
    .row.justify-content-center.mb-5
      .col-md-7.col-lg-4.mb-4.mb-lg-0
        .tarjeta.tarjeta-slide.arriba.color-primario(@mouseover="indicadorTarjetaSlide = false")
