              p #[b Segmentacin geogrfica]
              p.mb-5 Consiste en las necesidades e intereses que varan dependiendo de la ubicacin, clima y regin, permitiendo la venta y publicidad de las marcas, para, de este modo, expandir los negocios.
            .tarjeta-slide__img(:style="{'background-image': `url(${require('@/assets/curso/tema1/img45.svg')})`}")
      .col-md-7.col-lg-4.mb-4.mb-lg-0
        .tarjeta.tarjeta-slide.arriba.color-primario(@mouseover="indicadorTarjetaSlide = false")
          .indicador--hover(v-if="indicadorTarjetaSlide")
          .tarjeta-slide__contenedor
            .tarjeta-slide__contenido.p-4.p-xl-5
              figure.my-3
                img(src='@/assets/curso/tema1/img49.svg', style="width: 70px")
              p #[b Segmentacin demogrfica]
              p.mb-5 Es una de las formas ms utilizadas, que consiste en la divisin del mercado para conocer la forma en que los clientes usan los productos y servicios, adems del valor que estaran dispuestos a pagar por los mismos.
            .tarjeta-slide__img(:style="{'background-image': `url(${require('@/assets/curso/tema1/img46.svg')})`}")
      .col-md-7.col-lg-4
        .tarjeta.tarjeta-slide.arriba.color-primario(@mouseover="indicadorTarjetaSlide = false")
          .indicador--hover(v-if="indicadorTarjetaSlide")
          .tarjeta-slide__contenedor
            .tarjeta-slide__contenido.p-4.p-xl-5
              figure.my-3
                img(src='@/assets/curso/tema1/img50.svg', style="width: 70px")
              p #[b Segmentacin conductual]
              p.mb-5 Se basa en los comportamientos y la manera en que los clientes realizan sus procesos de compras y toma de decisiones; accin que permite al #[i marketing] un desarrollo con enfoque especfico.
            .tarjeta-slide__img(:style="{'background-image': `url(${require('@/assets/curso/tema1/img47.svg')})`}")
